// Hero 5
.section-hero-5 {
  .el-icon {
    .icon {
      position: relative;
      margin-left: 3px;
      animation: MoveUpDown 1s linear infinite;
      color: $white;
    }
  }
  .el-heading {
    width: 100% !important;
    max-width: 100% !important;

    h1 {
      font-size: 42px;
      color: $white;
    }
  }
}

// Hero 6
.section-hero-6 {
  &:before {
    top: 0;
    left: 0;
    width: 50%;
    height: 100%;
    z-index: 15;
    opacity: 0.75;
    content: "";
    position: fixed;
    background: $base-0;
  }
  .display-center {
    z-index: 18;
  }
}

@media (max-width: 992px) {
  .section-hero-4 {
    .el-particles {
      display: none !important;
    }
  }
}

@media (max-width: 768px) {
  .section-hero-2 {
    background-position: center left;
    .el-heading {
      float: inherit !important;
    }
    .display-center {
      z-index: 3;
    }
    &:after {
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      content: "";
      opacity: 0.45;
      position: absolute;
      //   background-color: $base-0;
    }
  }
  .section-hero-5 {
    .el-heading {
      h1 {
        font-size: 24px !important;
      }
    }
  }
}
